import React, { Component } from 'react';
import './Contact.css';
import '../../w3.css';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';

class Contact extends Component {
    render() { 
        return ( 
            <div>
                <section className="herow">
                    <div className="hero-body">
                        <div className="container">
                            <br></br>
                            <br></br>
                            <h1 className="title2" style={{textAlign: 'center', color: 'white', fontWeight: 'bold'}}>Contact Us</h1>
                        </div>
                    </div>
                </section>

                <section className="section" style={{background: 'linear-gradient(to bottom, #696969 0%, #c0c0c0 100%)'}}>
                            <br></br>
                            <br></br>
                            <br></br>

                            <div className="column">
                                <div className="cardes w3-animate-opacity" style={{backgroundColor: '#FF4500', padding: '20px', textAlign: 'center'}}>
                                    <h3 className="title" style={{color: 'white'}}><strong>Contact Information</strong></h3>
                                    <hr></hr>

                                    <h5><i className="fa fa-phone" style={{color: 'white'}}></i></h5>
                                    <p style={{textAlign: 'center', color: 'white'}}> 071 755 0990</p>

                                    <hr></hr>

                                    <h5><i className="fa fa-home" style={{color: 'white'}}></i></h5>
                                    <p style={{textAlign: 'center', color: 'white'}}>
                                        61 Chandler Crescent <br></br>
                                        Montclair <br></br>
                                        Durban <br></br>
                                        4004
                                    </p>

                                    <hr></hr>

                                    <h5><i className="fa fa-envelope" style={{color: 'white'}}></i></h5>
                                    <p style={{textAlign: 'center', color: 'white'}}> info@eagleropes.com</p>
                                </div>
                            </div>

                            <br></br>
                            <br></br>
                            <br></br>
                    <ScrollToTop showUnder={500} duration={1000} >
                        <img src={arrowUp} alt="Back to top" />
                    </ScrollToTop>
                </section>

                
            </div>
         );
    }
}
 
export default Contact;