import React, { Component } from 'react';
import './Team.css';
import '../../w3.css';
import kevin from '../../assets/kevin.jpg';
import azola from '../../assets/azola.jpg';
import enza from '../../assets/enza.jpg';
import mpilo from '../../assets/mpilo.jpg';
import nomfundo from '../../assets/nomfundo.jpg';
import patrick from '../../assets/patrick.jpg';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';


class Team extends Component {
    render() { 

        let heading = "Meet The Team";

        const email = () => {
            return window.location.href='mailto:info@eagleropes.com';
        };

        const emailAzola = () => {
            return window.location.href='mailto:Azola@eagleropes.com';
        };

        const emailMpilo = () => {
            return window.location.href='mailto:Mpilo@eagleropes.com';
        };

        const emailPatrick = () => {
            return window.location.href='mailto:Patrick@eagleropes.com';
        };

        const emailNomfundo = () => {
            return window.location.href='mailto:Nomfundo@eagleropes.com';
        };

        const emailEnza = () => {
            return window.location.href='mailto:Enza@eagleropes.com';
        };

        return ( 
            <div>
                <br></br>
                <br></br>

                <div className="container" style={{alignContent: 'center', padding: '25px'}}>
                <h1 className="title1"><strong>{ heading }</strong></h1>
                <hr></hr>
                <br></br>
                <br></br>
                    <div className="columns is-multiline">
                        <div className="column is-one-third w3-animate-left">
                            <div className="card">
                                <img src={kevin} alt="John" style={{width: "100%"}}/>
                                <h1 className="title">Kevin Jonas</h1>
                                <p>CEO</p>
                                <br></br>
                                <a href="https://wa.me/27717550990"><i className="fa fa-whatsapp"></i></a>
                                <a><i className="fa fa-twitter"></i></a>
                                <a><i className="fa fa-linkedin"></i></a>
                                <button onClick={email}>Contact</button>
                            </div>
                            <br></br>
                            <div className="card">
                                <img src={azola} alt="John" style={{width: "100%"}}/>
                                <h1 className="title">Azola Nyingwa</h1>
                                <p>COO</p>
                                <br></br>
                                <a><i className="fa fa-whatsapp"></i></a>
                                <a><i className="fa fa-twitter"></i></a>
                                <a><i className="fa fa-linkedin"></i></a>
                                <button onClick={emailAzola}>Contact</button>
                            </div>
                        </div>

                        <div className="column is-one-third w3-animate-right">
                        <div className="card">
                                <img src={patrick} alt="John" style={{width: "100%"}}/>
                                <h1 className="title">Patrick Makhaya</h1>
                                <p>Project Manager</p>
                                <br></br>
                                <a><i className="fa fa-whatsapp"></i></a>
                                <a><i className="fa fa-twitter"></i></a>
                                <a><i className="fa fa-linkedin"></i></a>
                                <button onClick={emailPatrick}>Contact</button>
                            </div>
                            <br></br>
                            <div className="card">
                                <img src={enza} alt="John" style={{width: "100%"}}/>
                                <h1 className="title">Enza Magubane</h1>
                                <p>Finance</p>
                                <br></br>
                                <a><i className="fa fa-whatsapp"></i></a>
                                <a><i className="fa fa-twitter"></i></a>
                                <a><i className="fa fa-linkedin"></i></a>
                                <button onClick={emailEnza}>Contact</button>
                            </div>
                        </div>

                        <div className="column is-one-third w3-animate-left">
                        <div className="card">
                                <img src={nomfundo} alt="John" style={{width: "100%"}}/>
                                <h1 className="title">Nomfundo Shandu</h1>
                                <p>Managing Director</p>
                                <br></br>
                                <a><i className="fa fa-whatsapp"></i></a>
                                <a><i className="fa fa-twitter"></i></a>
                                <a><i className="fa fa-linkedin"></i></a>
                                <button onClick={emailNomfundo}>Contact</button>
                            </div>
                            <br></br>
                            <div className="card">
                                <img src={mpilo} alt="John" style={{width: "100%"}}/>
                                <h1 className="title">Mpilo Mhlongo</h1>
                                <p>Marketing</p>
                                <br></br>
                                <a><i className="fa fa-whatsapp"></i></a>
                                <a><i className="fa fa-twitter"></i></a>
                                <a><i className="fa fa-linkedin"></i></a>
                                <button onClick={emailMpilo}>Contact</button>
                            </div>
                        </div>
                    </div>
                </div>

                    <br></br>
                    <br></br>
                    <br></br>
                    <br></br>

                    <ScrollToTop showUnder={500} duration={1000} >
                        <img src={arrowUp} alt="Back to top" />
                    </ScrollToTop>
                
            </div>
         );
    }
}
 
export default Team;