import React, { Component } from 'react';
import './Projects.css';
import kitchen from '../../assets/kitchen.jpg';
import boxes from '../../assets/boxes.jpg';
import feltex from '../../assets/feltexlogo.png';
import kzn from '../../assets/kzn.png';
import logo from '../../assets/vodacom-logo.png';
import nopa from '../../assets/nopa.jfif';
import roof from '../../assets/roof.jpg';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';

class Projects extends Component {
    render() { 
        return ( 
            <div>
                <section>
                    <div className="container" style={{alignContent: 'center', padding: '25px'}}> 
                    <br></br>
                        <h1 className="title1"><strong>Projects</strong></h1>
                        <hr></hr>

                        <div className="columns is=multiline">
                            
                            <div className="column is-one-quarter">
                                <div className="content">
                                    <img src={roof} alt="Makro" style={{width: '100%'}} />
                                    <h3>Renovations / Rope Access</h3>
                                    <p>Cleaning and renovations of roof at Makro warehouse (rope access).</p>
                                </div>
                            </div>

                            <div className="column is-one-quarter">
                                <div className="content">
                                    <img src={kitchen} alt="kitchen counter" style={{width: '100%'}} />
                                    <h3>Renovations</h3>
                                    <p>Recent renovations of the kitchens at the Durban University of Technology.</p>
                                </div>
                            </div>

                            <div className="column is-one-quarter">
                                <div className="content">
                                    <img src={boxes} alt="boxes" style={{width: '100%'}} />
                                    <h3>Goods Supply</h3>
                                    <p>Delivery of essential goods to Transnet.</p>
                                </div>
                            </div>

                            <div className="column is-one-quarter">
                                <div className="content">
                                    <img src={logo} alt="Vodacom" style={{width: '100%'}} />
                                    <h3>Rebranding</h3>
                                    <p>Rebranding of Vodacom in Durban (2013).</p>
                                </div>
                            </div>

                        </div>

                        <div className="columns is=multiline">
                            <div className="column is-one-quarter">
                                <div className="content">
                                    <img src={nopa} alt="NOPA" style={{width: '100%'}} />
                                    <h3>Cleaning</h3>
                                    <p>Cleaning of offices at NOPA.</p>
                                </div>
                            </div>

                            <div className="column is-one-quarter">
                                <div className="content">
                                    <img src={feltex} alt="Feltex" style={{width: '100%'}} />
                                    <h3>Cleaning</h3>
                                    <p>Cleaning of Feltex warehouses.</p>
                                </div>
                            </div>

                            <div className="column is-one-quarter">
                                <div className="content">
                                    <img src={kzn} alt="KZN" style={{width: '100%'}} />
                                    <h3>Goods Supply</h3>
                                    <p>Supplying of goods at the KZN Department of Health.</p>
                                </div>
                            </div>
                        </div>    
                    </div>

                    <br></br>
                    <br></br>

                    <ScrollToTop showUnder={400} duration={1000} >
                        <img src={arrowUp} alt="Back to top" />
                    </ScrollToTop>
                </section>
            </div>
         );
    }
}
 
export default Projects;