import React, { Component } from 'react';
import './Documents.css';
import bee from '../../assets/BEE 2.pdf';
import tax from '../../assets/Tax clearance.pdf';
import letter from '../../assets/letterhead.pdf';
import companyProfile from '../../assets/Company Profile 2021.pdf';
import CSD from '../../assets/CSDAudit.pdf';
import k20 from '../../assets/K2013128612.pdf';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';

class Documents extends Component {
    render() { 
        return ( 
            <div>
               <section className="section">
                   <div className="container">
                       <h1 className="title1"><strong>Documents</strong></h1>
                       <hr></hr>

                       <div className="columns is-multiline">
                           <div className="column is-one-third">
                                <a href={bee} target="_blank" rel="noopener noreferrer">B-BBEE Certificate</a>
                                <embed src={bee} alt="B-BBEE" style={{width: '600px', height: '500px'}}/>
                           </div>
                       
                            <div className="column is-one-third">
                                <a href={tax} target="_blank" rel="noopener noreferrer">Tax Clearance</a>
                                <embed src={tax} alt="Tax clearance" style={{width: '600px', height: '500px'}}/>
                            </div>

                            <div className="column is-one-third">
                                <a href={letter} target="_blank" rel="noopener noreferrer">Letterhead</a>
                                <embed src={letter} alt="Letterhead" style={{width: '600px', height: '500px'}}/>
                            </div>
                        </div>

                        <div className="columns is-multiline">
                            <div className="column is-one-third">
                                <a href={CSD} target="_blank" rel="noopener noreferrer">CSD Audit</a>
                                <embed src={CSD} alt="CSD Audit" style={{width: '600px', height: '500px'}}/>
                            </div>

                            <div className="column is-one-third">
                                <a href={companyProfile} target="_blank" rel="noopener noreferrer">Company Profile</a>
                                <embed src={companyProfile} alt="Company Profile" style={{width: '600px', height: '500px'}}/>
                            </div>

                            <div className="column is-one-third">
                                <a href={k20} target="_blank" rel="noopener noreferrer">Essential Services Certificate</a>
                                <embed src={k20} alt="Essential Services Certificate" style={{width: '600px', height: '500px'}}/>
                            </div>
                        </div>
                   </div>

                   <ScrollToTop showUnder={500} duration={1000} >
                        <img src={arrowUp} alt="Back to top" />
                    </ScrollToTop>
               </section>
            </div>
         );
    }
}
 
export default Documents;