import React, { Component } from 'react';
import './Home.css';
import { Link } from 'react-router-dom';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';

class Home extends Component {
    render() { 

        let heading = "Eagle Ropes";
        let subheading = "We serve to please you";

        return (
            <div>
                <section className="hero">
                    <div className="hero-body">
                        <div className="container">
                            <h1 className="title4" style={{color: 'white'}}>{ heading }</h1>
                            <h2 className="subtitles is-7">{ subheading }</h2>
                        </div>
                    </div>
                </section>

            <section className="section">
                    <div className="container">
                        <div className="columns pd is-desktop">
                        <div className="column is-1 has-text-centered">
                            <i className="fa fa-cog is-primary"></i>
                        </div>
                        <div className="column is-half-desktop">
                            <p className="title5"><strong>We strive to give our clients the best service and efficiency.</strong></p>
                        </div>
                        <div className="column">
                        </div>
                        </div>
                    

                    <div className="columns pd">
                        <div className="column">
                        <div className="card">
                            <div className="card-content">
                            <p className="title">We offer services in construction, renovations...</p>
                            <Link to="/about">
                                <button className="button is-medium is-black" id="learn">Learn more</button>
                            </Link>
                            </div>
                        </div>
                        </div>
                        <div className="column">
                        <div className="card">
                            <div className="card-content">
                            <p className="title">Meet all the team members involved...</p>
                            <Link to="/team">
                                <button className="button is-medium is-black" id="learn">Learn more</button>
                            </Link>
                            </div>
                        </div>
                        </div>
                        <div className="column">
                        <div className="card">
                            <div className="card-content">
                            <p className="title">See our official documentation and credentials...</p>
                            <Link to="/documents">
                                <button className="button is-medium is-black" href="#" id="learn">Learn more</button>
                            </Link>
                            </div>
                        </div>
                        </div>
                        </div>
                    </div>
                    <ScrollToTop showUnder={500} duration={1000} >
                        <img src={arrowUp} alt="Back to top" />
                    </ScrollToTop>
            </section>
            </div>
        );
    }
}
 
export default Home;