import React, { Component } from 'react';
import './Header.css';
import { Link } from 'react-router-dom';

class Header extends Component {

  constructor(props) {
      super(props);
      this.state = {isToggleOn: false};

      this.handleClick = this.handleClick.bind(this);
  }
  
  handleClick() {
      this.setState(prevState => ({
        isToggleOn: !prevState.isToggleOn
      }));
  }

  render() {

    let menuActive = this.state.isToggleOn ? 'is-active' : '';

      return (
        <div className="App">
            <div className="nav has-shadow">
              <div className="container">
                  <div className="nav-left">
                    <Link to="/" className="nav-item">Eagle Ropes</Link>
                  </div>

                  <span className={'nav-toggle '+menuActive} onClick={this.handleClick}>
                  <span></span>
                  <span></span>
                  <span></span>
                  </span>

                  <div className={'nav-right nav-menu '+menuActive}>
                    <Link to="/" className="nav-item r-item">Home</Link>
                    <Link to="/about" className="nav-item r-item">About</Link>
                    <Link to="/projects" className="nav-item r-item">Projects</Link>
                    <Link to="/team" className="nav-item r-item">The Team</Link>
                    <Link to="/contact" className="nav-item r-item">Contact</Link>
                  </div>
              </div>
            </div>
        </div>
      );
    }
  }

export default Header;
