import React, { Component } from 'react';
import './About.css';
import logo from '../../assets/vodacom-logo.png';
import transnet from '../../assets/transnet.jpg';
import feltex from '../../assets/feltexlogo.png';
import dut from '../../assets/dut.png';
import kzn from '../../assets/kzn.png';
import makro from '../../assets/makro.jpeg';
import '../../w3.css';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';

class About extends Component {
    render() { 
        return ( 
            <div>
                <section className="heros">
                    <div className="hero-body">
                        <div className="container">
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                            <br></br>
                        </div>
                    </div>
                </section>


                <section className="section" style={{backgroundColor: 'whitesmoke'}}>
                    <div className="container">
                    <h1 className="title3"><strong>About Us</strong></h1>
                    <hr></hr>
                        <p>Eagle Ropes (PTY) LTD is a multi functional company that was established in 2013. We offer services in construction, renovations, cleaning, rope access and supplying goods. We strive to give our clients the best service and efficiency. The company is 100% black owned and continues to create opportunities for the youth.</p>
                        <br></br>
                        <p>Some of our clients include:</p>

                        <div className="containerr w3-animate-left">
                            <img src={logo} alt="Vodacom" style={{width: '90px'}} />
                            <p><strong>Vodacom</strong></p>
                            <p>One of the biggest cellular and Internet Service Providers in South Africa.</p>
                        </div>

                        <div className="containerr w3-animate-right">
                            <img src={feltex} alt="Feltex" style={{width: '90px'}} />
                            <p><strong>Feltex</strong></p>
                            <p>The leading supplier of quality automotive acoustic, comfort and trim components.</p>
                        </div>

                        <div className="containerr w3-animate-left">
                            <img src={dut} alt="DUT" style={{width: '90px'}} />
                            <p><strong>Durban University of Technology</strong></p>
                            <p>One of the biggest universities in KZN, South Africa.</p>
                        </div>

                        <div className="containerr w3-animate-right">
                            <img src={makro} alt="Makro" style={{width: '90px'}} />
                            <p><strong>Makro</strong></p>
                            <p>Makro has 22 warehouse stores in South Africa trading in food, general merchandise and liquor.</p>
                        </div>

                        <div className="containerr w3-animate-left">
                            <img src={kzn} alt="KZN" style={{width: '90px'}} />
                            <p><strong>KZN Department of Health</strong></p>
                            <p>The mission of the KwaZulu-Natal Department of Health is to develop a sustainable, coordinated and comprehensive health system at all levels.</p>
                        </div>

                        <div className="containerr w3-animate-right">
                            <img src={transnet} alt="Transnet" style={{width: '90px'}} />
                            <p><strong>Transnet</strong></p>
                            <p>Transnet SOC Ltd is a large South African rail, port and pipeline company, headquartered in the Carlton Centre in Johannesburg.</p>
                        </div>
                    </div>
                    <ScrollToTop showUnder={500} duration={1000} >
                        <img src={arrowUp} alt="Back to top" />
                    </ScrollToTop>
                </section>
            </div>
         );
    }
}
 
export default About;